@import '../variables.scss';
.Form {
  position: relative;
  max-width: 400px;
  margin: 60px auto 0 auto;
  padding: 30px;
  text-align: left;
  border-radius: 10px;
  background-color: $white-gray;
}
.button--close {
  position: absolute;
  right: -40px;
  top: -40px;
  width: 40px;
  height: 40px;
  border: 3px solid $white;
  border-radius: 100%;
  color: $white;
  background-color: transparent;
  cursor: pointer;
}
.button--submit {
  border: none;
  border-radius: 6px;
  padding: 10px;
  font-size: 1rem;
  color: White;
  background-color: $submit-color;
  cursor: pointer;
  
}
